<template>
   <v-layout fill-height column justify-center  >
      <vue-easy-lightbox
         moveDisabled
         :visible="visible"
         :imgs="toArr(certificates)"
         :index='index'
         @hide='handleHide'
         :sstyle="lightBoxSettings"
         style='position: fixed; top:0; width: 100vw; height: 100vh'
               
         >
      
            <template v-slot:toolbar="{toolbarMethods}">
               
               <v-btn large style='top: 100vh; left: 45vw; transform: translateY(-200%)' 
                  :to='"/certificates/"+certificates[index].pdf'
                  target='_blank'
               >Download</v-btn>


            </template>
         </vue-easy-lightbox>
      
      <v-flex xs12  class=' '>

         <div class=' '  style=''> 
            <v-layout row fill-height wrap style='' ``
            >
               <v-flex 
                  xs12
                  md10
                  lg5
                  mt-2

                  order-lg2
                  
                  :pa-3='$vuetify.breakpoint.smAndDown'
                  :pa-5='$vuetify.breakpoint.mdAndUp'
                  
                  
               >

                  <v-layout row > 
                     <v-flex shrink class='ctitle-shape'>
                           <v-img height=40 style='transform-origin: center;transform: rotateY(45deg)' width=25 :src="require('@/assets/bullet.svg')"> </v-img>
                     </v-flex>
                     <v-flex class='ctitle'>
                           <h1 class='display-1' style='opacity: .5'> Certifications <span class='subheading'> </span> </h1>
                           
            
                     </v-flex>
                  </v-layout>

                  <div 
                           class='ctitle-underline mt-1'
                           style='
                                    width: 95%;
                                    height: 5px;
                                    background:  #afafaf;
                                    opacity: .65
                           '
                     >
                  </div>

                  <v-layout row justify-left  wrap  class="pr-5">
                     <v-flex grow   pa-2  v-for='(c,i,a) of logos' @click='cIndex=i' class='certi'>
                        <v-img  contain aspect-ratio=1  :src='c' style=' border: 1px solid rgba(0,0,0,.5);'> </v-img>
                     </v-flex>
                  </v-layout>
                  <br/>
                  <br/>

                  <v-layout row wrap>
                     <v-flex xs12 md8 style=''>
                        <h1 class='headline' style='opacity: .9'> Certification and Approvals</h1>

                        <ul>
                           <li  style='font-size: 1rem;' v-for='i of cInfo'> {{i}} </li>
                        </ul>

                        <br/>
                        <h1 class='headline' style='opacity: .9'> Product Compliance To </h1>

                        <v-layout row wrap>   
                           <v-flex xs12 md6 class='bullet' xs6 pl-2 style='font-size: 1rem;' v-for='i of compliance'> <ul> <li> {{i}} </li> </ul> </v-flex>
                        </v-layout>

                        <br/>
                        <h1 class='headline'> Indian Approvals </h1>
                        <ul>
                           <li v-for="(i,k) of indianApprovals"> {{i}} </li>
                        </ul>
                     </v-flex>
                  </v-layout>

               </v-flex>

               <v-flex xs10 lg7 mt-1  :wrap='$vuetify.breakpoint.smAndDown'
                  order-sm2
                  order-xs2
                  order-md1
                  
               >
                  <v-layout
                  ml-2 row fill-height justify-center align-center wrap  style=';overflow: hidden !important;  padding: 30px 0px 30px 0px' id='sstyle-1'>
                     <v-flex grow xs10 sm6 md3 pa-2 v-for='(c,i) of certificates' 
                        @click='handleExpand(i)'
                        class='cImg' style='margin: 3px;'
                     >
                        <v-img  cover :src='c.page'   aspect-ratio=.60> </v-img>
                     </v-flex>
                  </v-layout>
               </v-flex>




            </v-layout>
         </div>
      </v-flex>
   </v-layout>
</template>

<script>

import cdata from '../assets/certifications/new'


export default {

   components: {},
   data(){
      return {

         cIndex: null,
         index: null,
         data: [
           
         ],
         index: 0,
         visible: false,


         logos: [
            require('../assets/certifications/dnv.png'),
            require('../assets/certifications/nkk.png'),
            require('../assets/certifications/tuv_nord.png'),
            require('../assets/certifications/tuv_rhein.png'),
            require('../assets/certifications/irs.png'),
         ],

         cInfo: [
            'Quality System: ISO 9001',
            'Pressure Equipment Directive (PED)-EC 97/23/EC for fittings and valves',
            'DNV GL Type approval for Double compression and Hydraulic fittings',
            'ABS Type approval',
            'IRS Type approval',
            'NKK Japan Type approval',

         ],
         indianApprovals: [
            'Projects & Development India Ltd. (PDIL)',
            'Engineers India Ltd. (EIL)',
            'Mecon Limited',
            'Tractebel Engineering Pvt. Ltd.',
            'Udhe India Pvt. Ltd.',
            'Tata Consulting Engineers Ltd. (TCE)'
         ],

         compliance: [
            'ASME 31.3',
            'ASME 31.1',
            'ISO 8443-1',
            'ISO 8434-2',
            'CSA 51.9',
            'ECE 110',
            'ECE 67-1',
            'ISO 15500-19',
            'ISO 15916',
            'ASME section VII-UG-101(1)(b)(2)',
            'ANSI/AGA NGV 3.1-1995 CGA NGV 12-3-M95',
            'ANSI/AGA NGV 4.1-1999 CGA NGV 12.5-M99',
            'BS 4368-4',
            'ASTM F 1387',
            'MSS SP 99',
            'MSS SP 79',
            'MSS SP 83',
            'MSS SP 95',
            'MSS SP 97',
            'ASME B 16.11',
            'ASME B 16.34',
         ],

         
      }
   },
   methods: {
      doAnimation: function(){
         this.$anime({
            targets: '.ds-img',
            opacity: [0,1],
            translateY: ['10',0],
            delay: (el,i) => {
               return i*50
            },
            duration: 1000,
            easing: 'linear'

         }) },

         handleExpand(i){
            //alert('hi')
            this.index=i
            this.visible = true
            //alert(this.$parent.visible)
            
         },
         handleHide(){
            this.visible = false
         },
         toArr(obj){
            let res = []
            let i;

            

            obj.forEach( (k) => res.push(k.page) )
         
            return res
         },



      handlePageImg(e){
        /* let style = `
            transform: translate(50%,50%) scale(2.5)
         `
         Object.assign(e.target.style,{transform: `translate(50% 50%) scale(2.5)` })
         console.log('done')*/
         //e.target.style.cssText = style
         //alert(JSON.stringify(e.target))
      }
   },

   beforeRouteEnter(to,from,next)
   {
      //document.getElementById('ftr').classList.add('ftr')
      next()
   },

   beforeRouteLeave(to,from,next){

     // alert('leaving')
      //document.getElementById('ftr').classList.remove('ftr')

      this.$nextTick( ()=> {
         next()
      } )
      
   },

   mounted() {
         console.log(cdata)
   },

   computed: {
      certificates(){
         return cdata
      }
   }

}
</script>

<style>


   #style-1::-webkit-scrollbar-track
   {
   
   
      border-radius: 10px;
      background-color: #F5F5F5;
      margin: 20% 0 50%;
   


   }

   #style-1::-webkit-scrollbar
   {
      width: 18px;
      
     

      
   }

   #style-1::-webkit-scrollbar-thumb
   {
     
      border-radius: 10px;
      background-color: rgba(0,0,0,.2);
      
   }

    .innerGrid{
      overflow: hidden;  /* NEW */
      min-width: 0;      /* NEW; needed for Firefox */
   }


   .cImg  {
     border: 1px solid rgba(0,0,0,.3);
     margin: 5px;
   }

   .ccImg:nth-child(odd){
      transform:  translate(25%) rotate(-15deg);
      box-shadow: -5px 15px 0px 5px rgba(0,0,0,.3)
      
   }
   .ccImg:nth-child(even){
      transform:  translate(-10%) rotate(15deg);
      box-shadow: 5px 15px 0px 5px rgba(0,0,0,.3)
   }


   .ccImg:nth-child(1n){
      margin-bottom: -30%
   }

   .certi {
      opacity: 1;
      transition: all linear .2s;
      cursor: pointer;
   }
   .certi:hover{
      opacity: 1
   }

   .iimg{
      transition: all linear .2s;
   }

   .iimg:hover{

      cursor: pointer;
      transform: scale(1.2);
      

   }

   .iimg:hover:after{
      content: '';
      background: rgba(0,0,0,.1);
      width: 100%;
      height: 100%;

   }


   .hdr{
      z-index: 55;
      top: 0px;
   }

   #ftr {
      z-index: 55;
      bottom: 0px;
   }
   .fade-enter-active, .fade-leave-active {
   transition: all .5s;
   opacity: 1;
   }
   .fade-enter, .fade-leave-active {
   opacity: 0;
   }

     .gridWrap{
              display: grid;
              grid-template-columns: repeat(4,1fr);
              grid-gap: 0px !important;
              grid-auto-rows: auto;
              
       }

       .gridWrap > div{
              background: #ddd;
              padding: 1em !important;
       }

       .gridWrap > div:nth-child(odd){
              background: #eee;
       }


</style>
